import bus from "@/bus.js"
import axios from 'axios'
import watermark from '../plugins/watermark.js'
import {mapState, mapGetters, mapMutations} from 'vuex' //stategettersmutations
export default {
	data() {
		return {
			// curSelectIndex:0
		}
	},
	computed: {
		...mapState(['questionOriginData', 'answer_index']),
		curSelectIndex: function() {
			// 
			return this.questionOriginData[this.answer_index-1].selectIndex
		}
	},
	mounted(){

	},
	methods:{
		...mapMutations(['setQuestionOriginData', 'setUserStatus']),
		openMater: function(type,userId, typeIndex = 0) {
			this.$material ({
				entrance: type,
				user_id: userId,
				typeIndex: typeIndex
			})
		},
		openLogin: function() {
			this.$login ({
				loginSuccess: (data) => {
					// vuex
					this.setUserStatus(data)
					this.$router.push('inputMsg/q1');
				},
				// click: () => {
				// 	this.$router.push('')
				// }
			})
		},
		single_select_btn(index, aIndex = this.answer_index) {
			// aIndex 
			// index 
			const me =this;
			let stage_data = me.questionOriginData[aIndex-1].btns;
			for (let i = 0; i < stage_data.length; i++) {
				me.$set(stage_data[i], 'select', false)
			}
			me.$set(stage_data[index], 'select', true)
			me.setQuestionOriginData([stage_data,index])
		},
		downloadIamge(imgsrc, name, flag) {//
			var image = new Image();
		    //  Canvas 
		    image.setAttribute("crossOrigin", "anonymous");
		    image.onload = function() {
	      	var canvas = document.createElement("canvas");
	      	canvas.width = image.width;
	      	canvas.height = image.height;
	      	var context = canvas.getContext("2d");
	      	context.drawImage(image, 0, 0, image.width, image.height);
	      	if(flag) {
		          var url = canvas.toDataURL("image/jpeg"); //base64
		      }else{
		          var url = canvas.toDataURL("image/png"); //base64
		      }

	        var a = document.createElement("a"); // a
	        var event = new MouseEvent("click"); // 
	        a.download = name || "photo"; // 
	        a.href = url; // URLa.href
	        a.dispatchEvent(event); // a
		    };
		    image.src = imgsrc;
		},
		downs(url,flag){
			this.downloadIamge(url, 'pic', flag)
		},
		// 
		watermark: function(src) {
			const options = {
				init(img) {
					img.crossOrigin = 'anonymous'
				}
			};
			console.log('water')
			let src_=watermark([src], options)
				.image(watermark.text.lowerRight('Insight', '120px serif', '#fff', 0.9))
				.then(function(url) {
					return url
				});
				watermark.destroy()
				return src_
		},
	}
}